.radio-wrapper .radio-checked .radio-inner {
  border: solid 1px #228be6; }

.radio-wrapper .radio-checked .radio-inner::after {
  opacity: 1; }

.radio-wrapper span + span {
  padding-left: 8px;
  padding-right: 8px; }

.radio-wrapper span {
  cursor: pointer;
  vertical-align: sub;
  position: relative; }
  .radio-wrapper span .radio-input {
    cursor: pointer;
    display: none; }
  .radio-wrapper span .radio-inner {
    display: inline-block;
    width: 16px;
    height: 16px;
    border: solid 1px #ced4da;
    border-radius: 100px; }
  .radio-wrapper span .radio-inner::after {
    position: absolute;
    display: inline-block;
    top: 3px;
    left: 3px;
    width: 8px;
    height: 8px;
    border-radius: 8px;
    background: #228be6;
    opacity: 0;
    content: ' '; }

.radio-wrapper:hover span {
  border-color: #228be6;
  color: #212529; }
