.checkbox-wrapper .checkbox-checked .checkbox-inner {
  border: solid 1px #228be6;
  background: #228be6; }

.checkbox-wrapper .checkbox-checked .checkbox-inner::after {
  opacity: 1; }

.checkbox-wrapper span + span {
  padding-left: 8px;
  padding-right: 8px; }

.checkbox-wrapper span {
  cursor: pointer;
  vertical-align: sub;
  position: relative; }
  .checkbox-wrapper span .checkbox-input {
    cursor: pointer;
    display: none; }
  .checkbox-wrapper span .checkbox-inner {
    display: inline-block;
    width: 16px;
    height: 16px;
    border: solid 1px #ced4da;
    border-radius: 2px; }
  .checkbox-wrapper span .checkbox-inner::after {
    position: absolute;
    display: inline-block;
    top: 0px;
    left: 4px;
    width: 4.5px;
    height: 8px;
    -webkit-transform: rotate(45deg) scale(1);
            transform: rotate(45deg) scale(1);
    border: 2px solid #fff;
    border-top: 0;
    border-left: 0;
    background: #228be6;
    opacity: 0;
    content: ' '; }

.checkbox-wrapper:hover span {
  border-color: #228be6;
  color: #212529; }
