.not-found {
  height: 100vh;
  background: #5f3dc4;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column; }
  .not-found h2 {
    font-size: 1.5rem;
    color: white;
    font-weight: 400; }
  .not-found button {
    -webkit-transition: all .15s ease-in;
    transition: all .15s ease-in;
    background: white;
    border: 1px solid #dee2e6;
    width: 80px;
    height: 30px;
    border-radius: 0.2rem;
    cursor: pointer; }
  .not-found button:hover {
    color: rgba(34, 139, 230, 0.8); }
  .not-found button:active {
    color: #228be6; }
  .not-found button:focus {
    border: 1px solid #228be6;
    color: #228be6; }
