.box-simul {
  max-width: 1024px;
  margin: 0 auto; }
  .box-simul button {
    -webkit-transition: all .15s ease-in;
    transition: all .15s ease-in;
    background: white;
    border: 1px solid #dee2e6;
    width: 54px;
    height: 30px;
    border-radius: 0.2rem;
    cursor: pointer;
    font-size: 0.7rem; }
  .box-simul button:hover {
    color: rgba(34, 139, 230, 0.8); }
  .box-simul button:focus {
    border: 1px solid #228be6;
    color: #228be6; }
  .box-simul button:active {
    border: 1px solid #1971c2;
    color: #1971c2; }
  .box-simul button + button {
    margin-left: 0.1rem; }
  .box-simul .list-wrapper .box-result {
    padding: 1rem;
    min-height: 400px; }
    @media (max-width: 419px) {
      .box-simul .list-wrapper .box-result {
        padding: 0.2rem; } }
    .box-simul .list-wrapper .box-result .box-result-title {
      padding-bottom: 0.6rem;
      border-bottom: 1px solid #e9ecef;
      color: #343a40;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap; }
      .box-simul .list-wrapper .box-result .box-result-title .title-right {
        font-weight: bold;
        color: #c92a2a; }
    .box-simul .list-wrapper .box-result .box-result-list {
      display: flex;
      flex-wrap: wrap; }
      .box-simul .list-wrapper .box-result .box-result-list .result-item-object {
        font-size: 0.8rem;
        padding: 0.4rem;
        border-left: 1px solid #e9ecef;
        border-bottom: 1px solid #e9ecef; }
        .box-simul .list-wrapper .box-result .box-result-list .result-item-object .special {
          color: #e03131;
          font-weight: bold; }
  .box-simul .adsense {
    text-align: center; }
  .box-simul .list-wrapper .box-iteminfo {
    padding: 1rem; }
    @media (max-width: 419px) {
      .box-simul .list-wrapper .box-iteminfo {
        padding: 0.2rem; } }
    .box-simul .list-wrapper .box-iteminfo .box-iteminfo-title {
      padding-bottom: 0.6rem;
      border-bottom: 1px solid #e9ecef;
      color: #343a40;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap; }
      .box-simul .list-wrapper .box-iteminfo .box-iteminfo-title .iteminfo-right {
        width: 284px;
        padding: 0.2rem; }
    .box-simul .list-wrapper .box-iteminfo .box-iteminfo-list {
      font-size: 0.8rem;
      display: grid;
      grid-template-columns: repeat(3, 1fr); }
      @media (max-width: 767px) {
        .box-simul .list-wrapper .box-iteminfo .box-iteminfo-list {
          grid-template-columns: repeat(2, 1fr); } }
      .box-simul .list-wrapper .box-iteminfo .box-iteminfo-list .box-iteminfo-object {
        cursor: pointer;
        padding: 0.2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        border-left: 1px solid #e9ecef;
        border-bottom: 1px solid #e9ecef; }
        .box-simul .list-wrapper .box-iteminfo .box-iteminfo-list .box-iteminfo-object .imgSrc {
          width: 45px;
          height: 32px; }
          .box-simul .list-wrapper .box-iteminfo .box-iteminfo-list .box-iteminfo-object .imgSrc img {
            width: 35px;
            height: 32px; }
        .box-simul .list-wrapper .box-iteminfo .box-iteminfo-list .box-iteminfo-object .itemName {
          flex: 1 1; }
        .box-simul .list-wrapper .box-iteminfo .box-iteminfo-list .box-iteminfo-object .sumValue {
          padding-left: 6px;
          width: 44px;
          font-weight: bold; }
      .box-simul .list-wrapper .box-iteminfo .box-iteminfo-list .box-iteminfo-object:hover {
        cursor: pointer;
        background: rgba(34, 139, 230, 0.05); }
